//variables
$green: #01b74f;
$red: #d72638;

//global styles
* {
  box-sizing: border-box;
}

//nav
nav {
  font-family: "Souvenir";
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  img {
    width: 10%;
    @media only screen and (min-width: 768px) {
      width: 6%;
    }
    @media only screen and (min-width: 1024px) {
      width: 4%;
    }
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    li {
      float: right;
      padding: 10px;
      a {
        display: block;
        text-decoration: none;
        color: $green;
      }
    }
  }
}

//order
.order-contain {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

//menu item
.menuItem {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 250px;
  @media only screen and (min-width: 768px) {
    width: 50%;
  }
  @media only screen and (min-width: 1024px) {
    width: 30%;
  }
  img {
    width: 25%;
  }
  div.menuInfo {
    width: 70%;
    h3 {
      text-decoration: underline;
    }
  }
  button {
    color: $green;
  }
  #quantity-ticker {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    p {
      width: 30px;
      text-align: center;
    }
    button {
      width: 30px;
      background: none;
      border: none;
      height: 100%;
    }
  }

  &.unavailable {
    opacity: 0.5;
    filter: grayscale(100%);
    pointer-events: none;
  }
}

//bottom Button
.bottomButton {
  position: fixed;
  bottom: 2%;
  right: 2%;
  width: 30%;
  padding-top: 30%;
  border-radius: 100%;
  background-color: $green;
  @media only screen and (min-width: 768px) {
    width: 15%;
    padding-top: 15%;
  }
  @media only screen and (min-width: 1024px) {
    width: 10%;
    padding-top: 10%;
  }
  @media only screen and (min-width: 1280px) {
    width: 7%;
    padding-top: 7%;
  }
  p {
    color: white;
    text-decoration: none;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
}

//backend
#refresh-contain {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  #refresh-button {
    color: white;
    margin: 30px 0 0 0;
    width: 100%;
    background: $green;
    border-radius: 7px;
    @media only screen and (min-width: 768px) {
      width: 60%;
    }
    @media only screen and (min-width: 1024px) {
      width: 40%;
    }
  }
}

#backend-contain {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

//backend entry
div.backendEntry {
  margin: 30px 0;
  width: 100%;

  @media only screen and (min-width: 768px) {
    width: 45%;
  }
  @media only screen and (min-width: 1024px) {
    width: 30%;
  }
  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    label.line {
      margin: 10px 0;
      width: 100%;
      input,
      textarea {
        width: 100%;
        padding: 10px;
        border: none;
        border-bottom: 1px solid $green;
        line-height: initial;
      }
    }

    label.radio {
      margin: 0 !important;
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      input,
      textarea {
        background-color: $green;
      }
    }
  }

  .bottomInputs {
    width: 100%;
    display: flex;
  }

  button {
    margin-top: 10px;
    color: $red;
    background: none;
    border: none;
  }

  select {
    margin-left: 10px;
    width: 70px;
    background: none;
    border: none;
  }
}

//cart
#display-cart {
  display: block;
}
#hide-cart {
  display: none;
}

.show {
  display: block;
}

.hide {
  display: none;
}
.cart-contain {
  color: white !important;
  width: 100vw;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: $green;
  overflow-x: hidden;
  padding: 0 15px;
  @media only screen and (min-width: 768px) {
    padding: 0 100px;
  }
  @media only screen and (min-width: 1024px) {
    padding: 0 300px;
  }
  .cart-header {
    img {
      width: 10%;
      @media only screen and (min-width: 768px) {
        width: 6%;
      }
      @media only screen and (min-width: 1024px) {
        width: 4%;
      }
    }
    h1 {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  #total {
    border-top: 1px solid white;
    padding: 5px 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }

  .cartItem {
    margin: 30px 0;
    h3 {
      margin: 0;
    }
  }

  .cartInfo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
